@import url("https://fonts.googleapis.com/css?family=Quicksand");

* {
  font-family: Quicksand;
}

.note_list {
  padding: 20px;
}

.box {
  border: lightgray 1px solid;

  margin: 10px;
  padding: 10px;
  float: center;
}

.notes {
  display: inline-block;
  padding: 3px 15px;

  margin-right: 5px;
  margin-bottom: 5px;

  background-color: #eef7cb;
  border: 1px solid grey;
  border-radius: 20px;
}

input {
  margin-bottom: 10px;
  height: 20px;
  width: 200px;
}

.title_header {
  background-color: #efebe6;
  display: flex;
  margin: 1%;
  padding: 1%;
  font-family: Quicksand;
  text-align: left;
}

.subheader_btns {
  margin: 1%;
  padding: 1%;
}

.error {
  font-family: Quicksand;

  background-color: lightpink;
}

.btns_list {
  margin: 15px 25%;
}

.btn {
  margin: 0px 5px;
}

.note_editor_btn {
  margin: 0 .5% 0 .5%;
  padding: .5%;
}

.save {
  margin-left: 0px;
  padding: 3px 10px;
  font-weight: bold;
  float: right;
}

.logout {
  margin-right: 0px;
  padding: 5px 10px;
  font-weight: bold;
  float: right;
}

.singular_note {
  display: inline-block;
  width: 300px;
  height: 300px;
  margin: 5px;
}

/* ------------------------------------------------------------- */
/* COMPONENT: NOTE */

.title_section {
  display: flex;
  background-color: #eeeaea;
}

.title_section > div {
  flex: 1;
}

.title {
  display: flex;
  align-items: center;
  font-size: 150%;
  padding: 10px;
  width: 70%;
}

.pin_btn {
  width: 15%;
}

.body_section {
  background-color: #d9d9d9;
}

.textbox {
  resize: none;
  border: none;
  padding: 15px;
  width: 90%;
}

.trash_btn {
  float: right;
}

